@import 'var.scss';
@import 'general.scss';

.wrapper {
	display: none;
}
.main {
	background-color: #000;
	width: 100%;
  height: 100vh;
	background-image: url(../images/bg.jpg);
	background-size: cover;
	color: #fff;
	text-align: center;
	@media (max-width: 480px) {
		background-image: url(../images/bg_mob.jpg);
	}
	&_text-wrap {
		background-color: rgba(0,0,0,0.3);
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
	&_title {
		font-size: 88px;
		text-transform: uppercase;
		color: #fff;
		font-weight: 400;
		margin-bottom: 20px;
		z-index: 10;
		position: relative;
		transform: translateY(-15px);
	  opacity: 0;
	  transition: all 1s ease;
		&.active {
			transform: translateY(0px);
      opacity: 1;
		}
		@media (max-width: 768px) {
			font-size: 50px;
		}
		@media (max-width: 480px) {
			font-size: 36px;
			margin-bottom: 10px;
		}
	}
	&_description {
		text-transform: uppercase;
		font-size: 1rem;
		color: #fff;
		font-weight: 700;
		opacity: 0.7;
		display: block;
		margin-bottom: 60px;
		transform: translateY(15px);
	  opacity: 0;
	  transition: all 1s ease;
		&.active {
			transform: translateY(0px);
			opacity: 1;
		}
		@media (max-width: 480px) {
			margin-bottom: 30px;
			font-size: 11px;
		}
	}
	&_arrow {
		display: block;
		margin: 0 auto;
		width: 36px;
		height: 36px;
		// background-image: url(../images/arrow.svg);
		// background-repeat: no-repeat;
		// background-size: cover;
		// background-repeat: no-repeat;
		opacity: 0;
		transform: translateY(-10px);
		transition: all 1s ease 0.8s;
		&.active {
			opacity: 1;
			transform: translateY(0px);
		}
		svg {
			stroke: rgba(145,146,147,1);
			stroke-width: 1;
		}
		@media (max-width: 480px) {
			width: 20px;
			height: 20px;
		}
	}
}
.section {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
	&_text-wrap {
		color: #fff;
		position: absolute;
		top: 30%;
		left: 50%;
		padding-right: 20px;
		@media (max-width: 1100px) {
			padding-left: 40px;
		}
		@media (max-width: 768px) {
			padding-left: 20px;
		}
		@media (max-width: 480px) {
			left: 0%;
			width: 55%;
			padding-left: 10px;
			padding-right: 10px;
		}
	}
	&_img-wrap {
	  position: absolute;
	  clip: rect(auto,auto,auto,auto);
	  width: 50%;
	  height: 100%;
	  top: 0;
	  left: 0;
		padding-left: 20px;
		@media (max-width: 480px) {
			left: 55%;
		}
		@media (max-width: 340px) {
			left: 45%;
			width: 55%;
		}
	}
}
.section_1 {
	background: #E3BAB3;
	background: #cfcfcf;
	background-color: #535e6e;
	background: #e7e2e1;
}
.section_2 {
	background: #1d1e1f;
	background: #fff;

}
.section_3 {
	background: #eee9e6;
}

.mac {
  position: fixed;
  top: 25%;
  left: 45%;
	margin-left: -560px;
  z-index: 99;
	@media (max-width: 1300px) {
		margin-left: -460px;
	}
	@media (max-width: 1100px) {
		margin-left: -360px;
		left: 50%;
	}
	@media (max-width: 768px) {
		margin-left: -240px;
		top: 34%;
	}
	@media (max-width: 480px) {
		margin-left: -150px;
		left: 100%;
		top: 35%;
	}
}
.mockup {
  //
  // height: 100px;

	width: 560px;
  position: relative;
  z-index: 99;
	@media (max-width: 1300px) {
		width: 460px;
	}
	@media (max-width: 1100px) {
		width: 360px;
	}
	@media (max-width: 768px) {
		width: 240px;
	}
	@media (max-width: 480px) {
		width: 140px;
	}
	img {
		max-width: 100%;
	}
}
.work {
	width: 512px;
	height: 288px;
  position: absolute;
	top: 24px;
	left: 24px;
  z-index: 9;
	@media (max-width: 1300px) {
		width: 422px;
		height: 239px;
		top: 18px;
		left: 18px;
	}
	@media (max-width: 1100px) {
		width: 330px;
		height: auto;
		top: 15px;
		left: 15px;
	}
	@media (max-width: 768px) {
		width: 222px;
		top: 10px;
		left: 10px;
	}
	@media (max-width: 480px) {
		width: 130px;
		top: 5px;
		left: 5px;
	}
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}


.card {
	padding: 20px 0 0 0;
	color: #1d1e1f;
	z-index: 10;
	position: relative;
	&_title {
		text-transform: uppercase;
		font-size: 60px;
		font-weight: 400;
		margin-bottom: 10px;
		@media (max-width: 1100px) {
			font-size: 40px;
		}
		@media (max-width: 768px) {
			font-size: 32px;
		}
		@media (max-width: 480px) {
			font-size: 24px;
		}
	}
	&_subtitle {
		display: block;
		margin-bottom: 30px;
		// text-align: right;
		color: #535e6e;
		font-size: 1rem;
		@media (max-width: 1100px) {
			text-align: left;
		}
	}
}

.technologies {
	margin-bottom: 20px;
	display: flex;
	// justify-content: flex-end;
	flex-wrap: wrap;
	@media (max-width: 768px) {
		justify-content: flex-start;
	}
	&_item {
		color: #9a9a9a;
		border: 1px solid #9a9a9a;
		border-radius: 5px;
		padding: 3px 10px;
		font-size: 10px;
		margin-right: 6px;
		margin-bottom: 6px;
		text-transform: uppercase;
		@media (max-width: 480px) {
			padding: 2px 6px;
			margin-right: 3px;
			margin-bottom: 3px;
			font-size: 8px;
		}
		&:last-child {
			margin-right: 0;
		}
	}
}

.links {
	text-align: right;
	@media (max-width: 480px) {
		text-align: left;
	}
	&_item {
		margin-right: 20px;
		text-decoration: none;
		font-size: 12px;
		color: #da8f83;
		text-transform: uppercase;
		@media (max-width: 480px) {
			font-size: 10px;
		}
		&:last-child {
			margin-right: 0;
		}
		&:hover {
			color: darken(#da8f83, 10%);
		}
	}
}
