* {
	outline: none;
	box-sizing: border-box;
}
*::before, *::after {
	box-sizing: border-box;
}
html {
	scroll-behavior: smooth;
	font-size: 16px;
	@media (max-width: 1024px) {
		font-size: 15px;
	}
	@media (max-width: 768px) {
		font-size: 14px;
	}
	@media (max-width: 480px) {
		font-size: 12px;
	}
}
body {
	margin: 0;
	min-height: 100vh;
	font-family: 'Lato', sans-serif;
	background-color: #1d1e1f;
}
ul[class], ol[class] {
	list-style: none;
	padding: 0;
	margin: 0;
}
h1[class], h2[class], h3[class], h4[class], h5[class] {
	margin-top: 0;
}
.uppercase {
	text-transform: uppercase;
}
img {
	max-width: 100%;
	display: block;
}
input, button, textarea, select {
	font: inherit;
}

.link {
  color: $accent;
  &:hover {
    text-decoration: none;
  }
}

.nav-icon {
	cursor: pointer;
	margin-left: 10px;
	display: none;
  z-index: 120;
	@media (max-width: 768px) {
		display: block;
	}
  &.active {
    .nav-icon_line {
      &:nth-child(1) {
        transform: rotate(45deg) translate(0px, 3px);
      }
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        transform: rotate(-45deg) translate(1px, -5px);
      }
    }
  }
	&_line {
		width: 20px;
		height: 1px;
		margin-bottom: 5px;
		background-color: #000611;
    transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
		&:last-child {
      margin-bottom: 0;
    }
	}
}
